export default {
	data () {
		return {
			qukanTablist: ['晒单评价', '往期成交', '趣砍规则'],
			qukanTablistIndex: 0,
			value:1,
			goodsDetail:{},
			goodsId:'',
			socketTask:null,
			timer:null,
			recentCutUsers:[],
			goodsPriceNow:10099,
			cutCount:0,
			second:'10',
			count:100,
			s:9
		}
	},
	methods: {
		// 更换tab
		handleTabIndex (index) {
			this.qukanTablistIndex = index
		},
		// 步进器
		valChange(e) {
			console.log(e);
			this.value = e.value
		},
		// 跳转出价记录
		goToBids () {
			uni.navigateTo({
				url: '../Bids/Bids'
			})
		},
		// 获取趣砍商品详情
		async getQuKanGoodsDetail(){
			let res = await this.$fetch(this.$api.getQukanGoods,{goodId:this.goodsId},'GET')
			console.log(res);
			res.data.goods.images = res.data.goods.images.split(",")
			this.goodsPriceNow = res.data.goods.nowPrice
			this.goodsDetail  = res.data.goods
		},
		initTimer(){
			// 计时器正在进行中，退出函数
			if (this.timer != null) { 
			  return 
			};
			// 计时器为空，操作
			this.timer = setInterval(() => {
			  this.count --
			  if(this.count==0){
				  this.count=100;
				  this.s --;
				  this.second = '0'+s;
				  if(this.s == 0){
					  this.s = 10
					  this.second= '10';
				  }
			  }
			}, 10)          
		
		},
		cutPrice(){
			//
			uni.sendSocketMessage({
				data:'cut'+this.goodsId,
				success: (res) => {
					console.log(res);
				}
			})
		},
		closeWebSocket(){
			uni.closeSocket({
				complete: (res) => {
					console.log(res)
				}
			})
		},
		connectWebSocket() {
			console.log(this.goodsId);
			let token = uni.getStorageSync('token')
			console.log(`ws://60.180.141.124:8082/websocket/${token}`)
			this.socketTask=uni.connectSocket({
			  url: `ws://60.180.141.124:8082/websocket/${token}`,
			  complete: () => {
			  }
			});
			
			uni.onSocketOpen(res => {
				console.log(res)
				uni.sendSocketMessage({
					data:'enter:'+ this.goodsId
				})
			});
			uni.onSocketMessage(res => {
				console.log(res);
				var dataObject = JSON.parse(res.data);
				//console.log(this.recentCutUsers)
				console.log(dataObject.goodsId+"匹配"+this.goodsId)
				if(dataObject.goodsId==this.goodsId){
					this.second= '10'
					this.s = 9
					this.count=100
					console.log(dataObject);
					this.recentCutUsers.unshift({"name":dataObject.name,"image":dataObject.image,"price":this.goodsPriceNow - (this.cutCount*0.1)})
					// this.recentCutUsers.pop()
					this.cutCount=dataObject.cutCount
					this.goodsPriceNow=this.goodsPriceNow-(this.cutCount*0.1)
					
					console.log("匹配"+dataObject.cutCount)
				}
				//console.log(dataObject.goodsId+'+'+this.goodsId)
			})
		},
	},
	onLoad(options) {
		this.goodsId = options.id
		this.getQuKanGoodsDetail()
		this.initTimer()
		this.connectWebSocket()
	},
	onUnload() {
		this.closeWebSocket()
		clearInterval(this.timer)
	}
	
}
